.text-label {
    margin-bottom: 15px;
    padding: 5px 15px;
    background-color: #ebf2f4;
    border-radius: 7px;
    height: 80px;
}

.text-label .dropdown button {
    margin: 0;
    padding: 0;
}

.text-label button {
    color: #466df7;
}

.text-label button:hover {
    color: #02d0c0;
}

.text-label .fa-info-circle {
    margin-left: 6px;
    font-size: 16px;
}

.text-label .fa-pencil-square {
    margin-right: 10px;
    font-size: 20px;
    position: relative;
    top: 2px;
}

.text-label .none, .text-label .none:hover {
    color: transparent;
}

.text-label .title{
    height: 20px;
    margin-bottom: 5px;
    padding: 0;
}

.text-label .caution{
    height: 25px;
    margin-top: 5px;
    padding: 0;
}

.text-label .caution span{
    color: red;
}

.text-label input{
    /*width: 160px;*/
    width: 100%;
    margin-top: 5px;
    padding-right: 28px;
}

.current-value-drop-div {
    background-color: #7b8ff8;
}

.current-value-drop-div-value {
    margin: 0 5px;
    border-radius: 5px;
    background-color: white;
}

.text-label .info-circle {
    position: absolute;
    right: 7px;
    top: -1px;
}
