.spinner {
    margin: 5px auto;
    margin-bottom: 20px;
    width: 100px;
    height: 100px;
}

@keyframes animate-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes animate-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.inner {
    position: relative;
    top: 0px;
    width: 100px;
    height: 100px;
}

.ring-1 {
    position: absolute;
    top: 20px;
    left: 20px;
    width: 60px;
    height: 60px;
    border: 1px solid #4b68f9;
    border-color: transparent #02d0c0 transparent #4b68f9;
    border-radius: 50%;
    animation: animate-ring 1.5s linear infinite;
}

.ring-2 {
    position: absolute;
