.global-conf-form {
/*margin: 0px -15px;*/
padding: 0;
}


.global-conf-item {
    margin-bottom: 15px;
    padding: 5px;
    background-color: #ebf2f4;
    border-radius: 7px;

}

.dropdown button {
    margin-left: 4px;
    padding: 0 4px;
    font-size: 16px;
    border: none;
    background-color: inherit;
}

.dropdown button:hover {
    color: #02d0c0;
}

.global-conf-item .title{
    height: 20px;
    margin-bottom: 5px;
    padding: 0;
}

.global-conf-item .caution{
    height: 25px;
    margin-top: 5px;
    padding: 0;
}

.global-conf-item .caution span{
    color: red;
}

.global-conf-item input{
    width: 180px;
    margin-top: 5px;
}
