.page-spoiler {

}

.page-spoiler .page-spoiler-title {
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 15px;
}

.page-spoiler .page-spoiler-line {
    position: relative;
    top: 11px;
    width: 100%;
    border-bottom: 1px solid #02d0c0;
}

.page-spoiler .page-spoiler-button {
    width: 100%;
    cursor: pointer;
    padding: 0 0px;
}

.page-spoiler .page-spoiler-button-text {
    padding: 0 15px;
    position: relative;
    top: 0;
    left: 15px;
    z-index: 1;
    margin: 0;
    width: 250px;
    margin: 0;
    text-align: center;
    vertical-align: middle;
    background-color: #fff;
    border-radius: 7px;
    box-shadow: 0 0 2px #ccc;
}

.page-spoiler .page-spoiler-button-text .fa{
    margin-left: 5px;
}

.page-spoiler .inner-menu {
    padding-top: 15px;
}

.page-spoiler .page-spoiler-button:hover .page-spoiler-button-text{
    background-color: #e9f5ff;
}

.page-spoiler .page-spoiler-button:hover .page-spoiler-line{
    box-shadow: 0 0 1px #61fff3;
}
