.small-spinner {
    background-color: green;
    position: relative;
    width: 0px;
    height: 0px;
}

.small-spinner .s-spinner-hold {
    position: absolute;
    top: 7px;
    left: 7px;
    width: 26px;
    height: 26px;
    border: 2px solid #ebf2f4;
    border-radius: 50%;
}

.small-spinner .s-spinner {
    position: absolute;
    top: 7px;
    left: 7px;
    width: 26px;
    height: 26px;
    border: 2px solid red;
    border-color: #ebf2f4 #ebf2f4 #ebf2f4 transparent;
    border-radius: 50%;
    animation: rotate-ring 1s linear infinite;
}

@keyframes rotate-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes rotate-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
