.localization-toggle {
    height: 20px;
    font-size: 15px;
}

.localization-toggle button {
    background-color: transparent;
    color: #173ab6;
    padding: 0;
    margin-right: 5px;
    border: none;
    height: 20px;
    font-size: 15px;
}

.localization-toggle button:hover {
    color: #466df7;
}

.localization-toggle .checked {
    color: #02d0c0;
    cursor: default;
}

.localization-toggle .checked:hover {
    color: #02d0c0;
}
