.light-text-label {

}

.light-text-label input{
    padding-right: 25px;
    width: 200px;
}

.light-text-label .dropdown-description .dropdown-menu {
    position: absolute;
    top: 24px;
    left: -200px;
    width: 160px;
}

.light-text-label .dropdown-description button {
    margin: 0;
    padding: 0;
}

.light-text-label .dropdown-description button {
    position: absolute;
    top: -1px;
    left: 5px;
    color: #466df7;
}

.light-text-label .dropdown-description button:hover {
    color: #02d0c0;
}

.light-text-label .dropdown-description .fa-info-circle {
    font-size: 16px;
}

/*

*/
.light-text-label .dropdown-warning .dropdown-menu {
    position: absolute;
    top: 24px;
    left: -200px;
    width: 160px;
    background-color: #faa;
}

.light-text-label .dropdown-warning button {
    margin: 0;
    padding: 0;
}

.light-text-label .dropdown-warning button {
    position: absolute;
    top: -1px;
    right: 7px;
    color: #a00;
}

.light-text-label .dropdown-warning button:hover {
    color: #d00;
}

.light-text-label .dropdown-warning .fa-info-circle  {
    font-size: 16px;
}
