.radio-switch {

}

.radio-switch-item{
    margin: 0;
    padding: 0;
    cursor: pointer;
    color: #466df7;
}

.radio-switch-item:hover {
    color: #5376f9;
}

.radio-switch-item-name {
    margin-top: 5px;
    margin-left: 5px;
    float: left;
}

.radio-switch-outer-circle {
    padding: 0;
    width: 30px;
    height: 30px;
    border: 2px solid #647dfd;
    border-radius: 50%;
    float: left;
}

.radio-switch-inner-circle {
    margin: 6px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #647dfd;
    animation: animate-inner-disk 0.15s linear 1;
}

@keyframes animate-inner-disk {
    0% {
        margin: 13px;
        width: 0px;
        height: 0px;
    }
    60% {
        margin: 5px;
        width: 16px;
        height: 16px;
    }
    100% {
        margin: 6px;
        width: 14px;
        height: 14px;
    }
}

@-webkit-keyframes animate-inner-disk {
    0% {
        margin: 13px;
        width: 0px;
        height: 0px;
    }
    60% {
        margin: 5px;
        width: 16px;
        height: 16px;
    }
    100% {
        margin: 6px;
        width: 14px;
        height: 14px;
    }
}
