.header {
    padding: 0;
    padding-bottom: 10px;
    background-color: white;
    border-bottom: 1px solid #02d0c0;
    margin-top: 10px;
}

.header .header-logo img {
    background-color: white;
    height: 80px;
}

.header .header-logo{
    padding: 0;
    background-color: white;
}

.header .header-title{
    padding-left: 15px;
}

.header .title {
    font-size: 32px;
    padding: 0;
    margin: 0;
}

.gateway-id {
    padding: 0;
    margin: 0;
    font-size: 20px;
    color: #666;
}

.header .header-text{
    padding-left: 15px;
    float: left;
}

.header .header-logout{
    float: right;
}
