.logout-form {
    font-size: 20px;
}

.logout-form button {
    margin-top: 12px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: white;
    color: #466df7;
}

.logout-form button:hover, .logout-form button:focus {
    border: 2px solid #02d0c0;
    background-color: #e9f5ff;
    color: #466df7;
}

.logout-form button:focus {
    border: 2px dashed #02d0c0;
    background-color: white;
}

.logout-form button:active {
    border: 2px solid #02d0c0;
    background-color: #89a3ff;
    color: white;
}

.logout-form .fa-sign-out {
    margin-left: 10px;
    color: #173ab6;
}
