.additional-channels, .additional-channels-green {
    margin-bottom: 15px;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #d4eff4;
    border-radius: 7px;
    text-align: center;
    /*height: 100px;*/
}

.additional-channels-green {
    background-color: #dcf3d7;
}

.additional-channels .title {
    margin-bottom: 15px;
}

.additional-channels input {
    width: 100%;
    padding-right: 25px;
}

/*.additional-channels .dropdown-toggle {
    margin: 0;
    padding: 0;
}*/

.additional-channels .dropdown-toggle {
    color: #466df7;
}

.additional-channels .dropdown-toggle:hover {
    color: #02d0c0;
}

.additional-channels .fa-info-circle {
    margin-left: 6px;
    font-size: 16px;
}

.additional-channels .fa-pencil-square {
    margin-right: 10px;
    font-size: 20px;
    position: relative;
    top: 2px;
}

.additional-channels .none, .additional-channels .none:hover {
    color: transparent;
}

.additional-channels-check-box {
    position: absolute;
    top: 15px;
    left: 30px;
}

.additional-channels .field-title {
    margin-top: 3px;
    margin-bottom: 8px;
}

.additional-channels .input-div {
    margin: 5px -15px 0 -15px;
}

.additional-channels .input-div input{
    margin-bottom: 15px;
}

.additional-channels .additional-fields {
    margin: 0px -15px 0 -15px;
}


.additional-channels .info-dropdown {
    width: 100%;
    position: absolute;
    top: 5px;
    left: 0px;
}

.additional-channels .info-dropdown .dropdown-menu {
    margin-left: 40px;
    position: absolute;
    top: 120px;
    right: 40px;
}

.additional-channels .info-circle {
    display: block;
    position: absolute;
    top: 5px;
    right: 25px;
}

.additional-channels .info-error-dropdown {
    display: block;
}

.additional-channels .info-error-dropdown .dropdown-menu{
    background-color: #faa;
    width: 100%;
    position: absolute;
    top: 75px;
    right: 0px;
    padding: 7px;
}

.additional-channels .info-error {
    font-size: 20px;
    position: absolute;
    right: 2px;
    top: 2px;
    color: #a00;
}

.additional-channels .info-error:hover {
    color: #d00;
}
