.main-menu {
    margin: 15px -15px;

}
/*, .main-menu li a:focus*/
.main-menu li a {
    background-color:  #4b68f9;
    color: white;
}

.main-menu li a:hover, .main-menu li a:focus {
    background-color:  #9fadf8;
    color: #2445e7;
}

.main-menu li .selected {
    /*background-color: #7b8ff8;*/
    background-color:  #9fadf8;
    color: #2445e7;
}

.main-menu .menu-item {
    padding-right: 10px;
}

.main-menu .item-icon {
    margin-right: 10px;
}

.main-menu .state {
    position: absolute;
    top: 14px;
    right: 10px;
    font-size: 15px;
}

.main-menu-mode-toggle {
    padding-top: 15px;
    padding-bottom: 5px;
    border-bottom: 1px solid #4b68f9;
    border-left: 1px solid #4b68f9;
    border-right: 1px solid #4b68f9;
    text-align: center;
}
