.droping-list {

}

.droping-list select{
    width: 200px;
    background-color: white;
    border: 1px solid #aaa;
    height: 30px;
	border-radius: 4px;
	margin: 0;
    padding: 0 4px;
}

.droping-list .dropdown button {
    margin: 0;
    padding: 0;
}

.droping-list button {
    position: absolute;
    top: -2px;
    right: -19px;
    color: #466df7;
}

.droping-list button:hover {
    color: #02d0c0;
}

.droping-list .fa-info-circle {
    font-size: 16px;
}
