.main-page {
	/*margin: 0px -15px 15px 0;*/
	padding: 0 0 15px 0;
	margin: 15px;
	border-left: 1px solid #d7d7d7;
	border-right: 1px solid #d7d7d7;
}

.main-page-title{
	padding: 10px 0;
	margin-bottom: 15px;
	height: 40px;
	background-color: #7b8ff8;
	color: white;
}

.main-page-row {
	padding: 0;
}

.main-page-buttons-block {
	margin: 0 -10px;
}

/**
 *  Buttons
 */
 .main-page .bottom-buttons {
	 margin-top: -10px;
 }

.main-page .bottom-buttons .container-fluid{
    padding: 0;
}

.main-page .bottom-buttons li {
    padding: 10px 10px 0 10px;
}

.main-page .bottom-buttons button{
    border-radius: 10px;
    width: 100%;
}

.main-page .bottom-buttons .navbar {
    margin: 0;
}
