.check-box {
    margin-bottom: 15px;
    padding: 0px 15px;
    background-color: #ebf2f4;
    border-radius: 7px;
    height: 50px;
}

.check-box .dropdown button {
    margin: 0;
    padding: 0;
}

.check-box button {
    color: #466df7;
}

.check-box button:hover {
    color: #02d0c0;
}

.check-box .fa-info-circle {
    margin-left: 10px;
    font-size: 16px;
}

.check-box .fa-pencil-square {
    margin-right: 10px;
    font-size: 20px;
    position: relative;
    top: 2px;
}

.check-box .none, .check-box .none:hover {
    color: transparent;
}

.check-box .title{
    height: 20px;
    margin-bottom: 0px;
    padding: 0;
}

.check-box .caution{
    height: 25px;
    margin-top: 5px;
    padding: 0;
}

.check-box .caution span{
    color: red;
}

.check-box input{
    margin: 0px;
}

.input-check-box {
    margin-left: 8px;
    position:relative;
    top: 12px;
}

.check-box-description {

}

.check-box-requires {
    background-color: #fdf4e5;
}
