.frequency-expert-presets-form {
    padding: 0px;
    margin: 0;
}

.file-actions-buttons {
    margin-bottom: 15px;
}

.file-actions-buttons button{
    border-radius: 10px;
}

.file-actions-buttons i{
    margin-left: 5px;
}


.frequency-expert-presets-element {
    background-color: #ebf2f4;
    padding: 10px;
    height: 60px;
    border-radius: 10px;
    margin-bottom: 15px;
    text-align: center;
}

.frequency-expert-presets-form-title {
    margin-bottom: 15px;
}

.frequency-expert-presets-element-state {
    height: 20px;
}

.frequency-expert-presets-form-line{
    border-bottom: 1px solid #02d0c0;
    margin: 0 15px 15px 15px;
}

.frequency-expert-presets-form .frequency-expert-presets-element-button {
    width: 100%;
    height: 40px;
    margin: 0px;
    padding: 0px;
    border: 2px solid #02d0c0;
    border-radius: 10px;
    background-color: white;
    color: #173ab6;
}

.frequency-expert-presets-form .frequency-expert-presets-element-button:hover {
    background-color: #e9f5ff;
}

.frequency-expert-presets-form .frequency-expert-presets-element-button:active {
    background-color: #89a3ff;
    color: white;
}

.frequency-expert-presets-element-button {
    border-radius: 10px;
}

.frequency-expert-presets-element .info-error {
    font-size: 25px;
    color: #a00;
    width:40px;
    height: 42px;
    position: absolute;
    left: -2px;
    top: -11px;
}

.frequency-expert-presets-element .info-error:hover {
    color: #d00;
}

.frequency-expert-presets-element .dropdown-menu {
    padding: 5px;
    background-color: #faa;
}
