.about-view {

}

.about-view-group {
    border-bottom: 1px solid #02d0c0;
    padding: 0 0 15px 0;
margin: 15px 15px 0 15px;
}

.about-view .ender {
    border: none;
}

.about-view-group .group-title {
    font-size: 20px;
}

.about-view-group .item-value {
    padding-left: 30px;
}


/**
 *  Update panel
 */
.about-view-update-panel {
    padding: 0px;
    margin-top: 10px;
}

.about-view-update-panel .pnl {
    background-color: #daffcb;
    padding: 10px;
    margin-left: 40px;
    border-radius: 10px;
}

.about-view-update-panel .info-circle {
    float: left;
    padding: 0;
    font-size: 30px;
    color: #ffc600;
    text-shadow: white 0px 0px 3px;
}

.about-view-update-panel button{
    float: right;
    border: 1px solid #02d0c0;
    padding: 2px 4px;
    border-radius: 7px;
}

.about-view-update-panel button:hover, .about-view-update-panel button:focus{
    border: 1px solid #02d0c0;
}

.about-view-update-panel button:focus{
    border: 1px dashed #02d0c0;
}

.about-view-update-panel button:active {
    border: 1px solid #02d0c0;
}

.about-view-update-panel i{
    margin-right: 4px;
}
